import { React, useState } from "react";
import InputMask from "react-input-mask";
import "../../assets/styles/inputs/InputForm.css";

export default function InputForm({
    placeholder,
    type,
    name,
    id,
    min = null,
    max = null,
    onChange,
    value = null,
}) {

    //   text input,    
    const handleTextInputChange = (e) => {
        //          
        const regex = /^[\p{L}\p{N}\s.,-]*$/u;
        const value = e.target.value;

        if (regex.test(value) || value === "") {
            onChange(e);
        } else {
            //    ,       
            e.target.value = value.slice(0, -1); //   
            onChange(e); //  
        }
    };

    //   input,   max  min
    const handleInputChange = (e) => {
        onChange(e);
    };

    const [inputValue, setInputValue] = useState(value || "");


    //       
    const validateInputDate = (value, type) => {
        if (type === "day" || type === "month") {
            if (value.length > 2) return value.slice(-2);
            if (value && !/^\d{1,2}$/.test(value)) return "";
            return value.padStart(2, "0");
        }
        if (type === "year") {
            if (value.length > 4) return value.slice(-4);
            if (value && !/^\d{1,4}$/.test(value)) return "";
            return value.padStart(4, "0");
        }
        return value;
    };

    //   input  (, , )
    const handleInputDateChange = (e) => {
        const { value } = e.target;
    
        let [year, month, day] = inputValue.split("-");
    
        //     
        if (e.target.name === "year") {
            year = validateInputDate(value, "year");
        } else if (e.target.name === "month") {
            month = validateInputDate(value, "month");
        } else if (e.target.name === "day") {
            day = validateInputDate(value, "day");
        }
    
        //         ,  
        const formattedValue = `${year || "0000"}-${month || "00"}-${day || "00"}`;
    
        setInputValue(formattedValue); //   
    
        //   onChange      name  
        onChange({ target: { name, value: formattedValue } });
    };

    //    blur ( )
    const handleBlur = () => {
        const [year, month, day] = inputValue.split("-");
        const formattedValue = `${year || "0000"}-${month || "00"}-${day || "00"}`;
        setInputValue(formattedValue);
        onChange({ target: { name, value: formattedValue } });
    };

    //   ,  3   ,      
    if (type === "date" || (max || min)) {
        const [year, month, day] = inputValue.split("-");

        return (
            <div className="input-form-date-container">
                <div className="input-form-container">
                    <label htmlFor={id} className="input-form-label"></label>
                    <input
                        className="input-form-date"
                        type="text"
                        id={`${id}-year`}
                        name="year" //  name
                        value={year || ""}
                        placeholder=""
                        onChange={handleInputDateChange}
                        onBlur={handleBlur}
                    />
                </div>

                <div className="input-form-container">
                    <label htmlFor={id} className="input-form-label"></label>
                    <input
                        className="input-form-date"
                        type="text"
                        id={`${id}-month`}
                        name="month" //  name
                        value={month || ""}
                        placeholder=""
                        onChange={handleInputDateChange}
                        onBlur={handleBlur}
                    />
                </div>

                <div className="input-form-container">
                    <label htmlFor={id} className="input-form-label"></label>
                    <input
                        className="input-form-date"
                        type="text"
                        id={`${id}-day`}
                        name="day" //  name
                        value={day || ""}
                        placeholder=""
                        onChange={handleInputDateChange}
                        onBlur={handleBlur}
                    />
                </div>
            </div>
        );
    }

    // if (max || min) {
    //     return (
    //         <input
    //             className="input-form-date"
    //             type={type}
    //             id={id}
    //             name={name}
    //             value={value}
    //             placeholder={placeholder}
    //             min={min}
    //             max={max}
    //             onfocus="(this.type='date')"
    //             onblur="if(this.value==''){this.type='text'}"
    //             onChange={onChange}
    //         />
    //     );
    // }




    if (type === "coordinates") {
        return (
            <input
                className="input-form-coordinates"
                id={id}
                name={name}
                type="number"
                placeholder={placeholder}
                onChange={handleInputChange}
                value={value}
            />
        );
    }

    if (type === "tel") {
        return (
            <InputMask
                className="input-form"
                mask="+375 (99) 999 - 99 - 99"
                id={id}
                name={name}
                placeholder={placeholder}
                onChange={onChange}
                value={value}
            />
        );
    }

    return (
        <input
            className="input-form"
            id={id}
            name={name}
            type={type}
            placeholder={placeholder}
            value={value}
            onChange={
                type === "text" ? handleTextInputChange : handleInputChange
            }
        />
    );
}
